<template>
  <div class="tabs-box">
    <div class="tabs-head" ref="tabsHeadRef">
      <div
        class="tabs-select-item"
        v-for="(item, index) in number"
        :key="index"
        @click="tabsSwitch(index)"
        :ref="setTabsItemRef"
      >
        <span>标签{{ item }}</span>
      </div>
      <div class="line" ref="lineRef"></div>
    </div>
    <div class="tabs-track" ref="trackRef">
      <div class="tabs-body" v-for="(item, index) in number" :key="index">
        内容{{ item }}
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core'
const number = ref(4)
const select = ref(0)
const lineRef = ref()
const tabsItemRefs = []
const trackRef = ref()
const tabsHeadRef = ref()
function setTabsItemRef(el) {
  tabsItemRefs.push(el)
}

function tabsSwitch(index) {
  // const { left, width } = tabsItemRefs[index].getBoundingClientRect()
  const { left, width } =
    tabsItemRefs[index]['childNodes'][0].getBoundingClientRect()
  lineRef.value.style.width = width + 'px'
  lineRef.value.style.left = `${left + tabsHeadRef.value.scrollLeft}px`

  const mid = tabsHeadRef.value.clientWidth / 2
  const cur = left + width / 2
  const isLeft = mid > cur
  let scrollL = 0

  select.value = index
  const offsetX = index * -100
  trackRef.value.style.transform = `translateX(${offsetX}%)`

  if (isLeft && tabsHeadRef.value.scrollLeft) {
    scrollL = Math.abs(mid - cur) * -1
  } else if (!isLeft) {
    scrollL = Math.abs(mid - cur)
  }

  const move = setInterval(() => {
    if (Math.round(scrollL)) {
      tabsHeadRef.value.scrollLeft += scrollL / 10
      scrollL -= scrollL / 10
    } else {
      clearInterval(move)
    }
  }, 10)
}
onMounted(() => {
  tabsSwitch(0)
})
</script>

<style lang="scss" scoped>
* {
  -webkit-touch-callout: none; /*系统默认菜单被禁用*/
  -webkit-user-select: none; /*webkit浏览器*/
  -khtml-user-select: none; /*早期浏览器*/
  -moz-user-select: none; /*火狐*/
  -ms-user-select: none; /*IE10*/
  user-select: none;
}
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent; /* For some Androids */
}

.tabs-box {
  min-width: 375px;
  overflow: hidden;
  .tabs-head {
    display: flex;
    position: relative;
    background: #ffffff;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 10px 20px;
    justify-content: space-between;
    transition: all 0.4s;
    .tabs-select-item {
      flex: 1 0 auto; //很关键
      font-size: 14px;
      user-select: none; /* Standard syntax */
      text-align: center;
      margin: 0 14px;
      display: inline-block;
      cursor: pointer;
    }
    .line {
      position: absolute;
      background-color: rgba(255, 0, 0, 0.644);
      height: 3px;
      width: 15px;
      border-radius: 3px;
      bottom: 0;
      left: 28px;
      transition: all 0.4s;
    }
  }
  .tabs-track {
    display: flex;
    width: 100%;
    height: 100%;
    transition: all 0.4s; //开启切换动画
    .tabs-body {
      box-sizing: border-box;
      flex-shrink: 0;
      height: 100px;
      width: 100%;
      padding: 20px 30px;
      font-size: 16px;
      background-color: #ffffff;
      transition: all 0.4s;
    }
  }
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
</style>
